import React from 'react'
import { Router } from "@reach/router"
import classNames from 'classnames'

import Test from '../../containers/Test'
import ProgressBar from '../../components/ProgressBar'
import QuestionnaireTitle from '../../components/QuestionnaireTitle'

const TestRoute = (props) => {
    const id = props.params['*']
    const testId = (id === 'wait') ? '9' : id;
    const isRegularTest = (id !== 'wait' && id !== '10');

    return (
        <div>
            <ProgressBar testId={testId} />
            <div
                className={classNames(
                'container',
                'full-width'
                )}
            >
                {
                isRegularTest && <QuestionnaireTitle />
                }
                <Router basepath="/test">
                    <Test path="/:testId" />
                </Router>

            </div>
        </div>
    )
}

export default TestRoute
